import Endpoint from '@/services/Endpoint'

export default {
  loanTypes (payload) {
    return Endpoint().get('/s1/loan/loanType', {
      params: payload
    })
  },
  createLoanType (payload) {
    return Endpoint().post('/s1/loan/loanType', payload)
  },
  modifyLoanType (payload) {
    return Endpoint().patch('/s1/loan/loanType', payload)
  }
}
